'use client';

import { BeamsWithCollision } from '@/components/ui/BeamsWithCollision';
import { motion } from 'framer-motion';
import {
  FaYoutube,
  FaInstagram,
  FaFacebookF,
  FaTiktok,
} from 'react-icons/fa';
import { StarsBackground } from "@/components/ui/stars-background";
import { ShootingStars } from "@/components/ui/shooting-stars";

export default function Page() {
  return (
    
    <div className="w-full min-h-screen text-white font-inter">
      {/* Hero and Footer wrapper for beam effect */}
      <div className="relative overflow-hidden">
        {/* ✅ Beams only fall in this container */}
        <BeamsWithCollision number={4} className="absolute inset-0 z-0" />

        {/* Hero Section */}
         <div className="min-h-screen w-full bg-[linear-gradient(to_bottom,_#1B2B40_-150%,_transparent_60%)] text-white overflow-hidden relative">

      <div className="absolute top-0 left-1/2 -translate-x-1/2 w-[80vw] h-[80vh] bg-[radial-gradient(ellipse_at_top,_#4899E3_30%,_transparent_80%)] opacity-15 blur-2xl pointer-events-none z-0" />
        <section className="relative z-10 flex flex-col items-center justify-center min-h-[70vh] text-center">
          <StarsBackground className="z-0" />
          <ShootingStars className="z-20" />
          <img src="/icons/majarahlogo.svg" alt="Majarah Logo" className="w-70 h-auto mb-3" />
          <h1 className="text-4xl sm:text-5xl md:text-6xl font-aeonik font-light leading-tight">
            Don’t <span className="bg-gradient-to-r from-[#4899E3] to-[#8EC6FA] text-transparent bg-clip-text animate-gradient-x">Blend in</span>
            <br />Go  <span className="bg-gradient-to-r from-[#4899E3] to-[#8EC6FA] text-transparent bg-clip-text animate-gradient-x">Impact</span> with <span className="bg-gradient-to-r from-[#4899E3] to-[#8EC6FA] text-transparent bg-clip-text animate-gradient-x">Us</span>.
          </h1>
          <motion.a
            whileHover={{ scale: 1.05 }}
            href="#contact"
            className="inline-block mt-8 px-6 py-3 bg-gradient-to-r from-[#2A2B5F] to-[#3ABEFF] text-white rounded-full shadow-md hover:shadow-lg transition-all duration-300"
          >
            Contact Us Today →
          </motion.a>
        </section>
        </div>

        {/* Footer Section */}
        <footer className="relative z-10 text-white py-30 pb-15 px-6 md:px-20 items-center">
          <div className="max-w-7xl mx-auto grid grid-cols-1 md:grid-cols-4 gap-25">
           <div className="w-full flex justify-start mb-10">
  <div className="space-y-6 items-start text-left">
    <img src="/icons/majarahlogo.svg" alt="Majarah Logo" className="w-60 h-auto" />
    <p className="text-sm text-gray-400 ml-5 ">Transform the Digital Experience</p>
    <button className="px-5 py-2 rounded-full bg-white/10 border border-white/20 text-sm hover:bg-white/20 transition ml-15 mt-4">
      Contact Us
    </button>
  </div>
</div>

            <div className="space-y-5">
              <h4 className="font-semibold text-gray-200 mb-4">Quick Links</h4>
              <ul className="space-y-5 text-sm text-gray-400">
                <li><a href="#">About Us</a></li>
                <li><a href="#">Services</a></li>
                <li><a href="#">Our Work</a></li>
                <li><a href="#">The CEO</a></li>
                <li><a href="#">Contact</a></li>
                <li><a href="#">Privacy Policy</a></li>
              </ul>
            </div>

            <div className="space-y-3">
              <h4 className="font-semibold text-gray-200 mb-4">Contact</h4>
              <ul className="text-sm text-gray-400 space-y-5">
                <li>info@majarah.co</li>
                <li>www.majarah.co</li>
                <li>Dubai,<br />United Arab Emirates</li>
              </ul>
            </div>

            <div className="space-y-3">
              <h4 className="font-semibold text-gray-200 mb-4">Follow Us</h4>
              <div className="flex space-x-4 text-gray-400 text-xl">
                <FaYoutube className="hover:text-white transition" />
                <FaInstagram className="hover:text-white transition" />
                <FaFacebookF className="hover:text-white transition" />
                <FaTiktok className="hover:text-white transition" />
              </div>
            </div>
          </div>
            <p className="text-xs text-gray-600 pt-6  text-center mt-10">
                ©2025 Majarah Company, Inc. All right reserved
              </p>
        </footer>
      </div>
    </div>
  );
}
