'use client';

import { LayoutGroup, motion } from 'framer-motion';
import { StarsBackground } from "@/components/ui/stars-background";
import { ShootingStars } from "@/components/ui/shooting-stars";
import { HoverNavLink } from '@/components/ui/HoverNavLink';

export default function HeroSection() {
  const brands = ['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'h7', 'h8', 'h9', 'h10', 'h11', 'h12'];

  return (
    <div className="relative min-h-screen w-full text-white px-6 md:px-12 overflow-hidden">

      {/* Background Layers */}
      <div className="absolute inset-0 z-0 pointer-events-none">
        <StarsBackground className="z-0" />
        <ShootingStars className="z-20" />
      </div>

      {/* Foreground Content */}
      <div className="relative z-30">
        {/* Navbar */}
        <div className="flex justify-between items-center py-2">
          <img
            src="/icons/majarahlogo.svg"
            alt="Majarah Logo"
            className="w-60 h-auto"
          />
          <LayoutGroup id="navbar-links">
            <div className="font-poppins hidden md:flex space-x-2 text-sm relative mr-22">
              {["About Us", "Services", "Work", "Project"].map((item, index) => (
                <HoverNavLink key={index} label={item} href="#" />
              ))}
            </div>
          </LayoutGroup>

          {/* Contact Us Button */}
          <a
  href="#"
  className="inline-flex items-center justify-center h-10 px-6 rounded-full bg-[#1B2B40] border border-[#1B2B40] text-sm font-poppins text-white hover:bg-[#26364e] transition"
>
  Contact Us
</a>
        </div>

        <div className="border-b border-white/20 w-full" />

        {/* Hero Text Section */}
        <div className="relative md:mt-24 max-w-3xl mx-auto text-center z-30">
          <div className="relative z-10">
            <div className="relative inline-flex h-10 overflow-hidden rounded-full p-[1px] focus:outline-none focus:ring-2 focus:ring-[#8EC6FA]/60 focus:ring-offset-2 focus:ring-offset-[#1B2B40] mb-6">
              <span className="absolute inset-[-1000%] animate-[spin_8s_linear_infinite] bg-[conic-gradient(from_0deg_at_50%_50%,#77B8F3_0%,#FFFFFF_50%,#104677_100%)]" />
              <p className="inline-flex h-full w-full items-center justify-center rounded-full bg-[#1B2B40] px-8 py-2 text-sm font-poppins text-white/60 backdrop-blur-3xl transition z-10 relative">
                Creating Digital That Matters
              </p>
            </div>

            <motion.h1 className="font-aeonik text-white text-2xl sm:text-4xl md:text-6xl font-light leading-[1.4] gap-4">
              <div className="whitespace-nowrap">
                We Don’t{" "}
                <span className="bg-gradient-to-r from-[#4899E3] to-[#8EC6FA] text-transparent bg-clip-text animate-gradient-x">
                  Just Create Content
                </span>
              </div>
              <div>
                We Build{" "}
                <span className="bg-gradient-to-r from-[#4899E3] to-[#8EC6FA] text-transparent bg-clip-text">
                  Impact
                </span>
              </div>
            </motion.h1>

            <p
              className="font-poppins font-light text-sm mt-6 leading-[2.0]"
              style={{ color: '#F5F5F5' }}
            >
              Founded by Eisa Al Habib, Emmy® award-winning filmmaker and Forbes 30 Under 30 honoree,<br />
              Majarah is a full-scale media agency delivering next-level storytelling, influencer-driven campaigns,<br />
              and cross-platform digital marketing that moves people—and brands.
            </p>

            <motion.a
              href="#"
              whileHover={{ scale: 1.05 }}
              className="inline-block mt-15 px-6 py-2 border border-[#5AA5E9] bg-[linear-gradient(to_bottom,_#5AA5E9_-150%,_transparent_60%)] text-white rounded-full shadow-md hover:shadow-lg transition-all duration-300"
            >
              Let’s Work Together →
            </motion.a>
          </div>
        </div>

        {/* Logos Section */}
        <div className="mt-35 text-center">
          <p className="font-poppins font-regular text-lg text-gray-400 mb-6">
            Top-tier brands and governments redefining how stories are told online.
          </p>
          <div className="grid grid-cols-3 sm:grid-cols-4 md:grid-cols-6 gap-10 place-items-center max-w-5xl mx-auto mt-20">
            {brands.map((brand, i) => (
              <img
                key={i}
                src={`/brands/${brand}.svg`}
                alt={brand}
                className={`${
                  brand === 'h5' ? 'w-32 h-auto' : 'h-auto'
                } grayscale opacity-70 hover:opacity-100 transition`}
              />
            ))}
          </div>

          <motion.a
            href="#"
            whileHover={{ scale: 1.05 }}
            className="inline-block mt-10 px-6 py-2 font-inter font-semi-bold bg-[#343236] text-[#5AA5E9] tracking-[0.25em] text-sm rounded-full shadow-md hover:shadow-lg transition-all duration-300 mt-30"
          >
            OUR SERVICES
          </motion.a>
        </div>
      </div>
    </div>
  );
}
